import React, { useState, useEffect, useRef } from "react";
import classNames from "classnames/bind";
import {
  SendOutlined,
  CloseOutlined,
  LoadingOutlined,
} from "@ant-design/icons";

import styles from "./Navbar.module.scss";
import CardFilm from "./cardFilm";
import Comment from "./Comment";
import axios from "axios";
import Episode from "./episode/Episode";
import { Col, Row } from "antd";

const cx = classNames.bind(styles);

function Navbar({ user, episodeFilm, film }) {
  const [key, setKey] = useState("comment");
  const [comments, setComments] = useState([]);
  const [userComment, setUserComment] = useState("");
  const [commentParentID, setCommentParentID] = useState();
  const [isRepping, setIsRepping] = useState("");
  const [checkForComment, setCheckForComment] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const inputElement = useRef();

  const Films = [
    {
      href: "#",
      id: 1,
      img: "https://vnw-img-cdn.popsww.com/api/v2/containers/file2/cms_topic/doraemon_t_p_m_i_xem_ngay-1152f671c7e3-1667616000071-RwrJYn8Z.png?v=0&maxW=300&format=webp",
      name: "Doraemon",
      time: 1979,
      rate: "7+",
      product: "TV Asashi VN",
      des: "Doraemon c mua bn quyn v c cp nht pht sng mi nht trn ng dng gii tr POPS. y l b phim hot hnh chuyn th t truyn tranh hp dn nht Nht Bn: Doraemon ca tc gi Fujiko Fujio sng tc t nm 1969.B truyn k v mt ch mo my tn Doraemon n t th k 22  gip mt cu b lp 5 hu u tn l Nobi Nobita. Sewashi (Nobito), chu ba i ca Nobita gi Doraemon v qu kh nhm gip  ng mnh, qua  ci thin c hon cnh ca con chu Nobita sau ny. Cc tp phim Doraemon thng xoay quanh nhng rc ri hay xy ra vi cu b Nobita. Ct truyn thng gp nht l Nobita tr v nh khc lc vi Doraemon v nhng rc ri m cu gp phi  trng hc hoc vi bn b. Sau khi b cu van ni hoc thc gic, Doraemon s ly ra mt bo bi trong chic ti thn k trc bng  gip Nobita gii quyt rc ri ca mnh.Nhng Nobita s li thng i qu xa so vi d nh ban u ca Doraemon. Cu thng ly s u vit ca bo bi  tru gho mi ngi nn cui cng b phn tc dng, b bo bi gy phin phc. C i khi nhng ngi bn ca Nobita, thng l Suneo hoc Jaian li ly trm nhng bo bi v s dng chng khng ng mc ch. Kt thc mi cu chuyn, nhng ai s dng sai mc ch bo bi s phi chu hu qu do mnh gy ra, t  khn gi s rt ra c bi hc cho ring mnh. Doraemon l b phim hot hnh thiu nhi mang li cho khn gi nhng trng ci thoi mi, nhng tnh hung vui nhn cng nhng bi hc gio dc y  ngha. Phim c lng ting vi cht lng hnh nh sc nt mang li tri nghim xem phim tuyt vi. Xem trn b Doraemon full HD ngay ti POPS bn nh.",
    },
    {
      href: "#",
      id: 2,
      img: "https://vnw-img-cdn.popsww.com/api/v2/containers/file2/cms_topic/doraemon_t_p_m_i_xem_ngay-1152f671c7e3-1667616000071-RwrJYn8Z.png?v=0&maxW=300&format=webp",
      name: "Doraemon",
      time: 1979,
      rate: "7+",
      product: "TV Asashi VN",
      des: "Doraemon c mua bn quyn v c cp nht pht sng mi nht trn ng dng gii tr POPS. y l b phim hot hnh chuyn th t truyn tranh hp dn nht Nht Bn: Doraemon ca tc gi Fujiko Fujio sng tc t nm 1969.B truyn k v mt ch mo my tn Doraemon n t th k 22  gip mt cu b lp 5 hu u tn l Nobi Nobita. Sewashi (Nobito), chu ba i ca Nobita gi Doraemon v qu kh nhm gip  ng mnh, qua  ci thin c hon cnh ca con chu Nobita sau ny. Cc tp phim Doraemon thng xoay quanh nhng rc ri hay xy ra vi cu b Nobita. Ct truyn thng gp nht l Nobita tr v nh khc lc vi Doraemon v nhng rc ri m cu gp phi  trng hc hoc vi bn b. Sau khi b cu van ni hoc thc gic, Doraemon s ly ra mt bo bi trong chic ti thn k trc bng  gip Nobita gii quyt rc ri ca mnh.Nhng Nobita s li thng i qu xa so vi d nh ban u ca Doraemon. Cu thng ly s u vit ca bo bi  tru gho mi ngi nn cui cng b phn tc dng, b bo bi gy phin phc. C i khi nhng ngi bn ca Nobita, thng l Suneo hoc Jaian li ly trm nhng bo bi v s dng chng khng ng mc ch. Kt thc mi cu chuyn, nhng ai s dng sai mc ch bo bi s phi chu hu qu do mnh gy ra, t  khn gi s rt ra c bi hc cho ring mnh. Doraemon l b phim hot hnh thiu nhi mang li cho khn gi nhng trng ci thoi mi, nhng tnh hung vui nhn cng nhng bi hc gio dc y  ngha. Phim c lng ting vi cht lng hnh nh sc nt mang li tri nghim xem phim tuyt vi. Xem trn b Doraemon full HD ngay ti POPS bn nh.",
    },
    {
      href: "#",
      id: 3,
      img: "https://vnw-img-cdn.popsww.com/api/v2/containers/file2/cms_topic/doraemon_t_p_m_i_xem_ngay-1152f671c7e3-1667616000071-RwrJYn8Z.png?v=0&maxW=300&format=webp",
      name: "Doraemon",
      time: 1979,
      rate: "7+",
      product: "TV Asashi VN",
      des: "Doraemon c mua bn quyn v c cp nht pht sng mi nht trn ng dng gii tr POPS. y l b phim hot hnh chuyn th t truyn tranh hp dn nht Nht Bn: Doraemon ca tc gi Fujiko Fujio sng tc t nm 1969.B truyn k v mt ch mo my tn Doraemon n t th k 22  gip mt cu b lp 5 hu u tn l Nobi Nobita. Sewashi (Nobito), chu ba i ca Nobita gi Doraemon v qu kh nhm gip  ng mnh, qua  ci thin c hon cnh ca con chu Nobita sau ny. Cc tp phim Doraemon thng xoay quanh nhng rc ri hay xy ra vi cu b Nobita. Ct truyn thng gp nht l Nobita tr v nh khc lc vi Doraemon v nhng rc ri m cu gp phi  trng hc hoc vi bn b. Sau khi b cu van ni hoc thc gic, Doraemon s ly ra mt bo bi trong chic ti thn k trc bng  gip Nobita gii quyt rc ri ca mnh.Nhng Nobita s li thng i qu xa so vi d nh ban u ca Doraemon. Cu thng ly s u vit ca bo bi  tru gho mi ngi nn cui cng b phn tc dng, b bo bi gy phin phc. C i khi nhng ngi bn ca Nobita, thng l Suneo hoc Jaian li ly trm nhng bo bi v s dng chng khng ng mc ch. Kt thc mi cu chuyn, nhng ai s dng sai mc ch bo bi s phi chu hu qu do mnh gy ra, t  khn gi s rt ra c bi hc cho ring mnh. Doraemon l b phim hot hnh thiu nhi mang li cho khn gi nhng trng ci thoi mi, nhng tnh hung vui nhn cng nhng bi hc gio dc y  ngha. Phim c lng ting vi cht lng hnh nh sc nt mang li tri nghim xem phim tuyt vi. Xem trn b Doraemon full HD ngay ti POPS bn nh.",
    },
  ];

  const getComment = async () => {
    setIsLoading(false);

    try {
      const res = await (await axios.get("/api/user/comment")).data;
      setComments(res);
      setIsLoading(true);
    } catch (error) {
      console.log(error);
    }
  };

  const handleSendComment = async (comment) => {
    if (user) {
      const req = await axios.post("/api/user/comment", {
        userID: user.userId,
        commentParentID: commentParentID,
        comment: comment,
        filmID: 1,
        likeCount: 0,
      });
      setCheckForComment(!checkForComment);

      setCommentParentID();
      setUserComment("");
      setIsRepping("");
    }
  };

  useEffect(() => {
    getComment();
  }, [checkForComment]);

  return (
    <div className={cx("wrapper")}>
      <div className={cx("Tabs")}>
        <div
          className={cx("Tab", key === "episode" ? "active" : "")}
          onClick={() => setKey("episode")}
        >
          <span className={cx("Label")}>Tp</span>
        </div>
        <div
          className={cx("Tab", key === "more" ? "active" : "")}
          onClick={() => setKey("more")}
        >
          <span className={cx("Label")}>Xem thm</span>
        </div>
        <div
          className={cx("Tab", key === "comment" ? "active" : "")}
          onClick={() => setKey("comment")}
        >
          <span className={cx("Label")}>Bnh lun ({comments.length})</span>
        </div>
      </div>

      <div className={cx("container")}>
        {key === "more" ? (
          Films.map((film) => {
            return <CardFilm key={film.id} props={film}></CardFilm>;
          })
        ) : (
          <></>
        )}
        {key === "comment" ? (
          <div>
            {!isLoading ? (
              <div className={cx("loading-wrapper")}>
                <LoadingOutlined className={cx("loading-icon")} />
              </div>
            ) : (
              <div>
                <div className={cx("user-comment")}>
                  <div className={cx("user-comment-avatar")}>
                    <img
                      src="https://products.popsww.com/api/v2/containers/file2/profiles/Adult-01.png"
                      alt="Avatar"
                    />
                  </div>
                  <div className={cx("user-comment-input")}>
                    <input
                      type="text"
                      placeholder="Gi bnh lun"
                      ref={inputElement}
                      onChange={(e) => setUserComment(e.target.value)}
                      onKeyUp={(e) => {
                        if (e.key === "Enter") handleSendComment(userComment);
                      }}
                      value={userComment}
                    />
                  </div>
                  <div className={cx("user-comment-send_icon")}>
                    <SendOutlined
                      className={cx(userComment.trim() ? "active" : "")}
                      style={{ transform: "rotate(-45deg)" }}
                      onClick={
                        userComment.trim()
                          ? () => handleSendComment(userComment)
                          : null
                      }
                    />
                  </div>

                  <div className={cx("notify", isRepping ? "show" : "")}>
                    <span className={cx("notify-label")}>
                      Tr li {isRepping}
                    </span>
                    <CloseOutlined
                      className={cx("notify-icon")}
                      onClick={() => {
                        setCommentParentID();
                        setIsRepping("");
                      }}
                    />
                  </div>
                </div>
                <div className={cx("list-container")}>
                  {comments.map((comment) => {
                    return (
                      <Comment
                        key={comment.commentParent.commentID}
                        data={comment.commentParent}
                        commentChilds={comment.commentChild}
                        callBack={[setCommentParentID, setIsRepping]}
                        inputElement={inputElement}
                      ></Comment>
                    );
                  })}
                </div>
              </div>
            )}
          </div>
        ) : (
          <></>
        )}
        {key === "episode" ? (
          <Row gutter={[6, 6]}>
            {episodeFilm.map((episode) => {
              return (
                <Col span={4}>
                  <Episode href={"/WatchFilm/"+film.filmName+"?episode="+episode.episodeID} episode={episode?.episodeID}></Episode>
                </Col>
              );
            })}
          </Row>
        ) : (
          <></>
        )}
      </div>
    </div>
  );
}

export default Navbar;
